.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotateY(0deg); transform: rotateY(0deg); }
  to { -webkit-transform: rotateY(360deg); transform: rotateY(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotateY(0deg); transform: rotateY(0deg); }
  to { -webkit-transform: rotateY(360deg); transform: rotateY(360deg); }
}

.example-enter {
  position: absolute;
  width: 100vw;
  opacity: 0.01;
}

.example-enter.example-enter-active {
  position: absolute;
  width: 100vw;
  opacity: 1;
  -webkit-transition: opacity 500ms ease-in;
  -o-transition: opacity 500ms ease-in;
  transition: opacity 500ms ease-in;
}

.example-leave {
  position: absolute;
  width: 100vw;
  opacity: 1;
  -webkit-transform: translateY(0px);
      -ms-transform: translateY(0px);
          transform: translateY(0px);
}

.example-leave.example-leave-active {
  position: absolute;
  width: 100vw;
  opacity: 0.01;
  -webkit-transform: translateY(500px);
      -ms-transform: translateY(500px);
          transform: translateY(500px);
  -webkit-transition: opacity 500ms ease-in, -webkit-transform 500ms ease-in;
  transition: opacity 500ms ease-in, -webkit-transform 500ms ease-in;
  -o-transition: transform 500ms ease-in, opacity 500ms ease-in;
  transition: transform 500ms ease-in, opacity 500ms ease-in;
  transition: transform 500ms ease-in, opacity 500ms ease-in, -webkit-transform 500ms ease-in;
}

.counter-enter {
  position: absolute;
  width: 100vw;
  opacity: 0.01;
}

.counter-enter.counter-enter-active {
  position: absolute;
  width: 100vw;
