}

.Game-Over-leave {
  position: absolute;
  width: 100vw;
  opacity: 1;
  -webkit-transform: translateY(0px);
      -ms-transform: translateY(0px);
          transform: translateY(0px);
}

.Game-Over-leave.Game-Over-leave-active {
  position: absolute;
  width: 100vw;
  opacity: 0.01;
  -webkit-transform: translateY(500px);
      -ms-transform: translateY(500px);
          transform: translateY(500px);
  -webkit-transition: opacity 500ms ease-in, -webkit-transform 500ms ease-in;
  transition: opacity 500ms ease-in, -webkit-transform 500ms ease-in;
  -o-transition: transform 500ms ease-in, opacity 500ms ease-in;
  transition: transform 500ms ease-in, opacity 500ms ease-in;
  transition: transform 500ms ease-in, opacity 500ms ease-in, -webkit-transform 500ms ease-in;
}

@-webkit-keyframes GameOver-logo-shake {
  0% { -webkit-transform: translate(-10px); transform: translate(-10px); }
  50% { -webkit-transform: translate(10px); transform: translate(10px); }
  100% { -webkit-transform: translate(-10px); transform: translate(-10px); }
}

@keyframes GameOver-logo-shake {
  0% { -webkit-transform: translate(-10px); transform: translate(-10px); }
  50% { -webkit-transform: translate(10px); transform: translate(10px); }
  100% { -webkit-transform: translate(-10px); transform: translate(-10px); }
}



